.menu {
  background: #fafafa;
  border-right: 1px solid;
  margin-right: 18px;
  z-index: 9999;

  .dark-theme & {
    background: #252627;
  }

  @media #{$media-size-phone} {
    position: absolute;
    top: 50px;
    right: 0;
    border: none;
    margin: 0;
    padding: 10px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    font-size: 1rem;
    list-style: none;

    li {
      margin: 0 12px;
    }

    @media #{$media-size-phone} {
      flex-direction: column;
      align-items: flex-start;
      padding: 0;

      li {
        margin: 0;
        padding: 5px;
      }
    }
  }

  &-trigger {
    width: 24px;
    height: 24px;
    fill: currentColor;
    margin-left: 10px;
    cursor: pointer;
    display: none;

    @media #{$media-size-phone} {
      display: block;
    }
  }

  a {
    display: inline-block;
    margin-right: 15px;
    text-decoration: none;
    border-bottom: 3px solid transparent;
    padding: 3px 4px 2px;
    transition: all 0.2s ease;

    &:hover {
        border-color: #3d7b36;
    }

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.search-box input {
    cursor: pointer;
    width: 0;
    border-color: transparent;
    position: relative;
    height: 2rem;
    display: inline-block;
    font-size: .9rem;
    line-height: 2rem;
    padding: 0 .5rem 0 2rem;
    outline: none;
    transition: all .2s ease;
    background: #fafafa url(/images/search.svg) .6rem .3rem no-repeat;
    background-size: 1rem;

    .dark-theme & {
        background: #252627 url(/images/search.svg) .6rem .3rem no-repeat;
        background-size: 1rem;
        color: #a9a9b3;
        border: none;
    }

        &:focus {
            width: 10rem;
            cursor: text;
            border: 1px solid #cfd4db;
        }

        @media #{$media-size-phone}{
            width: 10rem;
            cursor: text;
            border: 1px solid #cfd4db;
        }
}