.page-search {
    h4 {
        margin-bottom: 0;
    }

    h4 a {
        padding-bottom: 0.7em;
        width: 100%;
        cursor: pointer;
    }

    .post-info {
        justify-content: space-between;
    }

    .summary {
        border-bottom: 1px grey dashed;
        padding-bottom: 20px;
    }

    .search-exit {
        float: right;
        font-weight: bold;
        margin-top: -15px;

        @media #{$media-size-phone} {
            float: none;
            margin-top: 0;
            width: 100%;
        }
    }
}
