.post {
    width: 100%;
    max-width: 800px;
    text-align: left;
    padding: 20px;
    margin: 20px auto;
    @media #{$media-size-tablet} {
        max-width: 600px;
        padding-top: 0;
    }

    &-date {
        &:after {
            content: '—';
        }
    }

    &-title {
        font-size: 2.625rem;
        margin: 10px 0 0;
        @media #{$media-size-phone} {
            font-size: 1.8rem;
        }

        a {
            text-decoration: none;
            opacity: 1;
        }
    }

    &-tags {
        display: block;
        margin-bottom: 20px;
        font-size: 1rem;
        opacity: 0.5;

        a {
            text-decoration: none;
        }
    }

    &-content {
        margin-top: 30px;
    }

    &-cover {
        border-radius: 8px;
        margin: 0 auto;
        max-width: 100%;
        max-height: 50vh;
        @media #{$media-size-tablet} {
            margin: 0;
            width: 100%;
        }
    }

    &-meta {
        margin-right: 0.5em;

        @media #{$media-size-tablet} {
            display: block;
            width: 100%;
            padding: 4px;
        }
    }

    &-info {
        margin-top: 0px;
        font-size: 0.8rem;
        line-height: normal;
        display: flex;
        flex-direction: row;
        @include dimmed;

        @media #{$media-size-tablet} {
            flex-direction: column;
        }

        p {
            margin: 0.8em 0;
        }

        a:hover {
            border-bottom: 1px solid white;
        }

        svg {
            margin-right: 0.2em;
        }

        .tag {
            margin-right: 0.5em;

            &::before {
                content: "#";
            }
        }

        .feather {
            display: inline-block;
            vertical-align: -.125em;
            width: 1em;
            height: 1em;
        }
    }

    .flag {
        border-radius: 50%;
        margin: 0 5px;
    }
}

.pagination {
    margin-top: 20px;

    &__title {
        display: flex;
        text-align: center;
        position: relative;
        margin: 20px 0;

        &-h {
            text-align: center;
            margin: 0 auto;
            padding: 5px 10px;
            background: $light-background;
            color: $light-color-secondary;
            font-size: 0.8rem;
            text-transform: uppercase;
            text-decoration: none;
            letter-spacing: 0.1em;
            z-index: 1;

            .dark-theme & {
                background: $dark-background;
                color: $dark-color-secondary;
            }
        }

        hr {
            position: absolute;
            left: 0;
            right: 0;
            width: 100%;
            margin-top: 15px;
            z-index: 0;
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        justify-content: center;

        a {
            text-decoration: none;
            font-weight: bold;
        }
    }
}

.button {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: $light-background-secondary;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 8px;
    max-width: 40%;
    padding: 0;
    cursor: pointer;
    appearance: none;

    .dark-theme & {
        background: $dark-background-secondary;
    }

    + .button {
        margin-left: 10px;
    }

    a {
        display: flex;
        padding: 8px 16px;
        text-decoration: none;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    &__text {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    &.next .button__icon {
        margin-left: 8px;
    }

    &.previous .button__icon {
        margin-right: 8px;
    }
}

.right-panel {
    position: fixed;
    right: 0;
    top: 20vh;
    border-left: 2px solid #bcbcbc;
    padding: 20px;
    width: 20%;

    ul {
        margin-left: 20px;
        padding: 0;
        list-style-type: none;
    }

    @media #{$media-size-small} {
        position: static;
        width: 100%;
        padding: 10px;
        border: none;
        margin-top: 20px;
        border-top: 2px solid #bcbcbc;
        border-bottom: 2px solid #bcbcbc;

    }
}